/**
 * Paywall 
 *  anon client
 */

import { supabase } from "./supabase-client"

export type PaywallState = {
  hasActiveSubscription: boolean
  unlockedPostIds: Set<string>
}

/**
 *  paywall 
 * @param userId  ID
 * @returns PaywallState  null
 */
export async function getMyPaywallState(userId: string): Promise<PaywallState | null> {
  try {
    // 1.  active subscription
    const { data: subscriptions, error: subError } = await supabase
      .from("subscriptions")
      .select("id, status, current_period_end")
      .eq("user_id", userId)
      .eq("status", "active")
      .gt("current_period_end", new Date().toISOString())

    if (subError) {
      console.error("[paywall] getMyPaywallState subscriptions error:", subError)
      return null
    }

    const hasActiveSubscription = subscriptions && subscriptions.length > 0

    // 2.  unlocked posts
    const { data: unlocks, error: unlockError } = await supabase
      .from("post_unlocks")
      .select("post_id")
      .eq("user_id", userId)

    if (unlockError) {
      console.error("[paywall] getMyPaywallState post_unlocks error:", unlockError)
      return null
    }

    const unlockedPostIds = new Set<string>(
      unlocks?.map((u) => u.post_id) || []
    )

    return {
      hasActiveSubscription,
      unlockedPostIds,
    }
  } catch (err) {
    console.error("[paywall] getMyPaywallState exception:", err)
    return null
  }
}

/**
 *  30 upsert subscription
 * @param userId  ID
 * @returns true false 
 */
export async function subscribe30d(userId: string): Promise<boolean> {
  try {
    const now = new Date()
    const periodEnd = new Date(now.getTime() + 30 * 24 * 60 * 60 * 1000) // +30 days

    //  upsertON CONFLICT user_id DO UPDATE
    const { error } = await supabase
      .from("subscriptions")
      .upsert(
        {
          user_id: userId,
          status: "active",
          current_period_end: periodEnd.toISOString(),
        },
        {
          onConflict: "user_id",
        }
      )

    if (error) {
      console.error("[paywall] subscribe30d error:", error)
      return false
    }

    return true
  } catch (err) {
    console.error("[paywall] subscribe30d exception:", err)
    return false
  }
}

/**
 *  postPPV unlock
 * @param userId  ID
 * @param postId Post ID
 * @returns true false unique 
 */
export async function unlockPost(
  userId: string,
  postId: string
): Promise<boolean> {
  try {
    const { error } = await supabase.from("post_unlocks").insert({
      user_id: userId,
      post_id: postId,
    })

    if (error) {
      // unique 
      if (error.code === "23505") {
        return true
      }
      console.error("[paywall] unlockPost error:", error)
      return false
    }

    return true
  } catch (err) {
    console.error("[paywall] unlockPost exception:", err)
    return false
  }
}

