"use client"

import { useState, useEffect } from "react"
import { useRouter } from "next/navigation"
import { Heart, Lock, DollarSign, Bookmark, MessageCircle } from "lucide-react"
import { NavHeader } from "@/components/nav-header"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar"
import { LockBadge } from "@/components/lock-badge"
import { LockOverlay } from "@/components/lock-overlay"
import { PaywallModal } from "@/components/paywall-modal"
import type { Post } from "@/lib/types"
import { ensureProfile, getCurrentUser } from "@/lib/auth"
import { supabase } from "@/lib/supabase-client"
import { getMyPaywallState, subscribe30d, unlockPost } from "@/lib/paywall"
import Link from "next/link"

// Mock data
const mockPosts: Post[] = [
  {
    id: "1",
    creatorId: "c1",
    creator: {
      id: "c1",
      username: "sophia_creative",
      email: "sophia@example.com",
      role: "creator",
      avatar: "/female-creator-avatar.jpg",
      subscriptionPrice: 9.99,
      isVerified: true,
    },
    type: "free",
    content: "Check out my latest work! More exclusive content coming soon for subscribers.",
    mediaUrl: "/creative-artwork-preview.jpg",
    createdAt: "2024-01-15T10:30:00Z",
    likes: 234,
    isLiked: false,
    isUnlocked: true,
    isSaved: false,
  },
  {
    id: "2",
    creatorId: "c2",
    creator: {
      id: "c2",
      username: "alex_pro",
      email: "alex@example.com",
      role: "creator",
      avatar: "/male-creator-avatar.jpg",
      subscriptionPrice: 14.99,
      isVerified: true,
    },
    type: "subscribers",
    content: "Behind the scenes content exclusively for my subscribers!",
    mediaUrl: "/behind-the-scenes-locked-content.jpg",
    createdAt: "2024-01-15T09:15:00Z",
    likes: 156,
    isLiked: false,
    isUnlocked: false,
    isSaved: false,
  },
  {
    id: "3",
    creatorId: "c3",
    creator: {
      id: "c3",
      username: "emma_artist",
      email: "emma@example.com",
      role: "creator",
      avatar: "/artist-creator-avatar.jpg",
      subscriptionPrice: 19.99,
      isVerified: false,
    },
    type: "ppv",
    price: 25,
    content: "Premium content available for purchase!",
    mediaUrl: "/premium-exclusive-content-locked.jpg",
    createdAt: "2024-01-14T18:45:00Z",
    likes: 89,
    isLiked: true,
    isUnlocked: false,
    isSaved: false,
  },
]

export default function HomePage() {
  const router = useRouter()
  const [posts, setPosts] = useState(mockPosts)
  const [filter, setFilter] = useState<"all" | "free" | "unlocked">("all")
  const [paywallOpen, setPaywallOpen] = useState(false)
  const [selectedPaywall, setSelectedPaywall] = useState<{
    type: "subscribe" | "ppv"
    postId: string
    creatorName: string
    creatorAvatar?: string
    price: number
  } | null>(null)
  const [isLoading, setIsLoading] = useState(true)
  const [paywallState, setPaywallState] = useState<{
    hasActiveSubscription: boolean
    unlockedPostIds: Set<string>
  } | null>(null)
  const [paywallError, setPaywallError] = useState<string | null>(null)
  const [currentUserId, setCurrentUserId] = useState<string | null>(null)

  const currentUser = {
    username: "john_doe",
    role: "fan" as const,
    avatar: "/fan-user-avatar.jpg",
  }

  //  paywall state
  const loadPaywallState = async (userId: string) => {
    try {
      setPaywallError(null)
      const state = await getMyPaywallState(userId)
      if (state) {
        setPaywallState(state)
      } else {
        setPaywallError(" paywall ")
      }
    } catch (err) {
      console.error("[home] loadPaywallState error", err)
      setPaywallError(" paywall ")
    }
  }

  //  profile  session  profile 
  useEffect(() => {
    const checkAuthAndProfile = async () => {
      try {
        //  supabase.auth.getSession() session  push /auth
        const {
          data: { session },
          error: sessionError,
        } = await supabase.auth.getSession()

        if (sessionError || !session) {
          console.log("[home] No session, redirecting to /auth")
          router.push("/auth")
          return
        }

        //  session  ensureProfile() profiles 
        console.log("[home] Has session, ensuring profile for user:", session.user.id)
        await ensureProfile()
        
        //  userId  paywall state
        setCurrentUserId(session.user.id)
        await loadPaywallState(session.user.id)
      } catch (err) {
        console.error("[home] checkAuthAndProfile error", err)
        // 
        router.push("/auth")
      } finally {
        setIsLoading(false)
      }
    }
    checkAuthAndProfile()
  }, [router])

  if (isLoading) {
    return (
      <div className="min-h-screen bg-background flex items-center justify-center">
        <div className="text-muted-foreground">Loading...</div>
      </div>
    )
  }

  // 
  if (paywallError) {
    return (
      <div className="min-h-screen bg-background">
        <NavHeader user={currentUser} notificationCount={3} />
        <main className="container max-w-2xl mx-auto px-4 py-6">
          <div className="bg-destructive/10 border border-destructive/20 rounded-lg p-4">
            <p className="text-destructive font-medium"></p>
            <p className="text-sm text-muted-foreground mt-1">{paywallError}</p>
            <Button
              className="mt-4"
              onClick={() => {
                setPaywallError(null)
                if (currentUserId) {
                  loadPaywallState(currentUserId)
                }
              }}
            >
              
            </Button>
          </div>
        </main>
      </div>
    )
  }

  const handleLike = (postId: string) => {
    setPosts((prev) =>
      prev.map((post) =>
        post.id === postId
          ? {
              ...post,
              likes: post.isLiked ? post.likes - 1 : post.likes + 1,
              isLiked: !post.isLiked,
            }
          : post,
      ),
    )
  }

  const handleSave = (postId: string) => {
    setPosts((prev) =>
      prev.map((post) =>
        post.id === postId
          ? {
              ...post,
              isSaved: !post.isSaved,
            }
          : post,
      ),
    )
  }

  const handleUnlock = (
    postId: string,
    type: "subscribe" | "ppv",
    creatorName: string,
    creatorAvatar: string | undefined,
    price: number,
  ) => {
    setSelectedPaywall({ type, postId, creatorName, creatorAvatar, price })
    setPaywallOpen(true)
  }

  const handlePaymentSuccess = async () => {
    if (selectedPaywall && currentUserId) {
      try {
        // 
        if (selectedPaywall.type === "subscribe") {
          const success = await subscribe30d(currentUserId)
          if (!success) {
            setPaywallError("")
            return
          }
        } else if (selectedPaywall.type === "ppv") {
          const success = await unlockPost(currentUserId, selectedPaywall.postId)
          if (!success) {
            setPaywallError("")
            return
          }
        }

        //  paywall state DB 
        await loadPaywallState(currentUserId)
        setPaywallOpen(false)
        setSelectedPaywall(null)
      } catch (err) {
        console.error("[home] handlePaymentSuccess error", err)
        setPaywallError("")
      }
    }
  }

  const getPaywallBenefits = () => {
    if (selectedPaywall?.type === "subscribe") {
      return [
        "Access to all subscribers-only content",
        "Support the creator directly",
        "New content delivered regularly",
      ]
    }
    return ["Instant access to this exclusive content", "Unlock forever - watch anytime", "High-quality media included"]
  }

  //  paywall state  post  isUnlocked
  const postsWithUnlockState = posts.map((post) => {
    // free post 
    if (post.type === "free") {
      return { ...post, isUnlocked: true }
    }

    // paid post paywall state 
    if (paywallState) {
      if (post.type === "subscribers") {
        // subscribers post active subscription
        return { ...post, isUnlocked: paywallState.hasActiveSubscription }
      } else if (post.type === "ppv") {
        // ppv post post 
        return { ...post, isUnlocked: paywallState.unlockedPostIds.has(post.id) }
      }
    }

    // 
    return { ...post, isUnlocked: false }
  })

  const filteredPosts = postsWithUnlockState.filter((post) => {
    if (filter === "all") return true
    if (filter === "free") return post.type === "free"
    if (filter === "unlocked") return post.isUnlocked
    return true
  })

  return (
    <div className="min-h-screen bg-background">
      <NavHeader user={currentUser} notificationCount={3} />

      <main className="container max-w-2xl mx-auto px-4 py-6">
        <div className="flex gap-2 mb-6 overflow-x-auto pb-2">
          <Button
            variant={filter === "all" ? "default" : "outline"}
            onClick={() => setFilter("all")}
            className={filter === "all" ? "" : "bg-transparent"}
          >
            All Posts
          </Button>
          <Button
            variant={filter === "free" ? "default" : "outline"}
            onClick={() => setFilter("free")}
            className={filter === "free" ? "" : "bg-transparent"}
          >
            Free
          </Button>
          <Button
            variant={filter === "unlocked" ? "default" : "outline"}
            onClick={() => setFilter("unlocked")}
            className={filter === "unlocked" ? "" : "bg-transparent"}
          >
            Unlocked
          </Button>
        </div>

        {/* Posts Feed */}
        <div className="space-y-6">
          {filteredPosts.map((post) => (
            <Card key={post.id} className="overflow-hidden">
              {/* Creator Header */}
              <div className="p-4 flex items-center justify-between">
                <Link href={`/creator/${post.creator.username}`} className="flex items-center gap-3 flex-1">
                  <Avatar className="w-10 h-10">
                    <AvatarImage src={post.creator.avatar || "/placeholder.svg"} />
                    <AvatarFallback>{post.creator.username[0].toUpperCase()}</AvatarFallback>
                  </Avatar>
                  <div className="flex-1">
                    <div className="flex items-center gap-2">
                      <span className="font-semibold text-foreground">{post.creator.username}</span>
                      {post.creator.isVerified && (
                        <svg className="w-4 h-4 text-primary" fill="currentColor" viewBox="0 0 20 20">
                          <path
                            fillRule="evenodd"
                            d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z"
                            clipRule="evenodd"
                          />
                        </svg>
                      )}
                    </div>
                    <span className="text-sm text-muted-foreground">{formatDate(post.createdAt)}</span>
                  </div>
                </Link>
                <LockBadge type={post.type} price={post.price} isUnlocked={post.isUnlocked} />
              </div>

              {/* Post Content */}
              <div className="px-4 pb-3">
                <p className="text-foreground">{post.content}</p>
              </div>

              {/* Media */}
              <div className="relative bg-muted aspect-video">
                <img
                  src={post.mediaUrl || "/placeholder.svg"}
                  alt="Post media"
                  className="w-full h-full object-cover"
                />
                {!post.isUnlocked && (
                  <LockOverlay
                    type={post.type === "ppv" ? "ppv" : "subscribe"}
                    price={post.price}
                    onUnlock={() =>
                      handleUnlock(
                        post.id,
                        post.type === "ppv" ? "ppv" : "subscribe",
                        post.creator.username,
                        post.creator.avatar,
                        post.type === "ppv" ? post.price || 0 : post.creator.subscriptionPrice || 0,
                      )
                    }
                  />
                )}
              </div>

              {/* Actions */}
              <div className="p-4 flex items-center justify-between">
                <div className="flex items-center gap-4">
                  <button
                    onClick={() => handleLike(post.id)}
                    className="flex items-center gap-2 text-muted-foreground hover:text-foreground transition-colors group"
                  >
                    <Heart
                      className={`w-5 h-5 transition-all ${post.isLiked ? "fill-red-500 text-red-500 scale-110" : "group-hover:scale-110"}`}
                    />
                    <span className="text-sm font-medium">{post.likes}</span>
                  </button>
                  <button
                    onClick={() => handleSave(post.id)}
                    className="flex items-center gap-2 text-muted-foreground hover:text-foreground transition-colors group"
                  >
                    <Bookmark
                      className={`w-5 h-5 transition-all ${post.isSaved ? "fill-current scale-110" : "group-hover:scale-110"}`}
                    />
                  </button>
                  <button className="flex items-center gap-2 text-muted-foreground hover:text-foreground transition-colors">
                    <MessageCircle className="w-5 h-5" />
                    <span className="text-xs text-muted-foreground">Coming soon</span>
                  </button>
                </div>
                {!post.isUnlocked && (
                  <Button
                    size="sm"
                    onClick={() =>
                      handleUnlock(
                        post.id,
                        post.type === "ppv" ? "ppv" : "subscribe",
                        post.creator.username,
                        post.creator.avatar,
                        post.type === "ppv" ? post.price || 0 : post.creator.subscriptionPrice || 0,
                      )
                    }
                  >
                    {post.type === "ppv" ? (
                      <>
                        <DollarSign className="w-4 h-4 mr-1" />
                        Unlock ${post.price}
                      </>
                    ) : (
                      <>
                        <Lock className="w-4 h-4 mr-1" />
                        Subscribe ${post.creator.subscriptionPrice}/mo
                      </>
                    )}
                  </Button>
                )}
              </div>
            </Card>
          ))}
        </div>
      </main>

      {/* Paywall Modal/Sheet */}
      {selectedPaywall && (
        <PaywallModal
          open={paywallOpen}
          onOpenChange={setPaywallOpen}
          type={selectedPaywall.type}
          creatorName={selectedPaywall.creatorName}
          creatorAvatar={selectedPaywall.creatorAvatar}
          price={selectedPaywall.price}
          benefits={getPaywallBenefits()}
          onSuccess={handlePaymentSuccess}
        />
      )}
    </div>
  )
}

function formatDate(dateString: string) {
  const date = new Date(dateString)
  const now = new Date()
  const diff = now.getTime() - date.getTime()
  const hours = Math.floor(diff / (1000 * 60 * 60))

  if (hours < 24) return `${hours}h ago`
  const days = Math.floor(hours / 24)
  return `${days}d ago`
}
